import React from 'react';

import { CardList } from "./components/card-list/card-list.component.jsx";

import { SearchBox } from "./components/search-box/search-box.component.jsx";

import './App.css';

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      monsters: [],
      searchbox: ''
    };
    
    //Case1
    // this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount() {
    fetch('https://jsonplaceholder.typicode.com/users')
    .then(response => response.json())
    .then(users => this.setState({ monsters: users}))
  }

  //Case1
  // handleChange (e) {
  //   this.setState({searchbox:e.target.value})
  // }

  handleChange = e => {
    this.setState({searchbox:e.target.value})
  }

  render() {
    const {monsters, searchbox} = this.state;
    const lowercasedSearchbox = searchbox.toLocaleLowerCase();
    const filteredMonsters = monsters.filter(monster => {
      return (
        monster.name.toLowerCase().indexOf(lowercasedSearchbox) >= 0 ||
        monster.email.toLowerCase().indexOf(lowercasedSearchbox) >= 0
        );
      });

    return (
      <div className="App">
        <h1> Monsters Rolodex Thien1997 </h1>
        <SearchBox placeHolder='Search monsters' handleChange={this.handleChange}/>
        <CardList monsters={filteredMonsters} />            
      </div>
    )
  }
}

export default App;
